import styled from 'styled-components'

export default styled.div`
  .list{
    padding: 15px;
    .item{
      padding: 15px;
      margin-bottom: 15px;
      background: #fff;
    }
    dl{
      display: flex;
      margin-bottom: 5px;
      dt{
        flex-shrink: 0;
        color: #888;
        width: 100px;
      }
    }    
  }
`